.container__pokedex {
  background-image: url(../../assets/bg.png);
  background-repeat: repeat;
  height: auto;
  min-height: 100vh;
}

.app__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 0 !important;
  margin: auto;
  width: 100%;
  text-align: center;
  height: 90%;
}

.arrow__back {
  position: absolute;
  top: 2rem;
  left: 4rem;
  font-size: 48px;
  color: rgb(70, 70, 70);
}

.arrow__back:hover {
  color: rgb(39, 39, 39);
}
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}

.header__title {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__items {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 70px;
}
.pokemon__container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 80%;
  transition: all 4s ease-in;
}
.all__container {
  display: grid;
  grid-template-columns: repeat(5,1fr);
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  width: 100%;
  height: auto;
  max-height: 80vh;
  overflow: auto;
  padding: 2rem;
  transition: all 0.3s ease-in;
}

.all__container button{
  background-color: transparent;
  border: none;
}

.pokemon__selected{
  height: 600px;
  width: 400px;
  background-color: white;
  margin-left: 50px;
  display: none;
  flex-direction: column;
  padding: 10px;
  border-radius: 20px;
}

.show__pokemon-selected{
  display: flex;
}

.pokemon__details button{
  position: absolute;
  background-color: transparent;
  border: none;
  cursor: pointer;
  right: 200px;
  font-size: 30px;
  transition: all 4s ease;
}
.pokemon__selected img{
  height: 200px;
  width: 200px;
}